.login{
    background-color: #f8f8f8;
    height: 100vh;
    width: 100vw;
    display: grid;
    place-items: center;
}

.login__imgLogo{
    max-height: 10rem;
    max-width: 10rem;
    border-radius: 10rem;
    object-fit: contain;
    margin-bottom: 2rem;
}


.login__container {
    padding: 6.25rem;
    text-align: center;
    background-color: white;
    border-radius: 0.625rem;
    box-shadow: 0px 2px 4px -3px rgba(0,0,0,0.8);

}

.login__container > button {
    text-transform: inherit!important;
    background-color: rgba(22, 70, 184, 0.896);
    color: white;
}

.login__container > button:hover {
    background-color: rgba(22, 71, 184, 0.76);
}